[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none !important;
}
.avatar.bordered {
  border: 3px solid fade(#fff, 50%);
}
.overflow-hidden {
  overflow: hidden;
}

.hwa3d {
transform: translate3d(0,0,0);
}


// Colors
$gray: #f4f4f4;
html {
  overflow: hidden;
  font-family: Open sans, Roboto, 'Helvetica Neue', sans-serif;
}
body {
  font-family: Open sans, Roboto, 'Helvetica Neue', sans-serif !important;
  line-height: 25px;
  height: 100%;
}
p,
.md-body-1 {
  line-height: 25px;
}
.mdi::before {
  font-size: 24px;
  position: relative;
  top: 6px;
}
.main-layout.dashboard {
  padding-top: 56px;
}
/* @media only screen and (max-height: 500px) {
    body {
        padding-top: 48px;
    }
} */
@media only screen and(orientation: landscape) and(max-height: 667px) {
  .main-layout.dashboard {
    padding-top: 64px;
  }
}
@media only screen and(orientation: landscape) and (max-height: 667px) and (max-width: 959px) {
  .main-layout.dashboard {
    padding-top: 64px;
  }
}
@media only screen and(orientation: landscape) and (min-height: 667px) and (min-width: 959px) {
  .main-layout.dashboard {
    padding-top: 64px;
  }
}
@media only screen and(orientation: landscape) and (max-width: 959px) {
  .main-layout.dashboard {
    padding-top: 64px;
  }
  md-toolbar{
      height: 64px !important;

      .md-toolbar-tools{
          height: 64px !important;
          max-height: 64px !important
      }
  }
}
.app-header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 90;
}
.main-layout {
  position: relative;
}
.content-feeds > div:first-child {
  height: auto !important;
}
.no-bg {
  background: none !important;
}
.entrance-bg {
  background: url(../img/1.jpg) no-repeat 0 0 /cover;
}
.login-bg {
  background: url(../img/concert.jpg) no-repeat 0 0 /cover;
}
.pull-right{
    float: right !important;
}
// Angular
[ng-click] {
  &:hover {
    cursor: pointer;
  }
}
// Main content bg color
.bg-light-gray {
  background-color: $gray !important;
}
// Navigation drawer pattern: https://www.google.com/design/spec/patterns/navigation-drawer.html#navigation-drawer-specs
md-sidenav {
  & > md-toolbar {
    padding: 15px;
    height: 150px;
    max-height: 150px;
  }
  & md-list-item.md-2-line .md-list-item-text {
    padding-top: 14px;
    padding-bottom: 12px;
  }
  & md-divider {
    margin-top: 4px;
    margin-bottom: 4px;
  }
}
// White svg icons in toolbar and fab
md-sidenav {
  md-icon {
    fill: #737373;
    position: relative;
  }
  .md-avatar {
    height: 64px;
    width: 64px;
  }
}
/* More closely matches https://www.google.com/design/spec/components/lists.html#lists-specs */
md-list-item.md-3-line {
  md-divider {
    &[md-inset] {
      left: 70px;
    }
  }
}
// Round avatar
.square-img-container {
  position: relative;
  overflow: hidden;
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
    /* initial ratio of 1:1*/
  }
  & img.square2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
.square-img-container img {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  max-width: 200%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.square-img-container img.potrait {
  width: 100%;
  height: auto;
}
img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
  
}
.left-post-toolbar {
  & >* {
    width: 100%;
  }
}
/* fade animation */
.animate-show {
  transition: 0.75s ease-out opacity;
  opacity: 1;
  &.ng-hide {
    height: 0;
    min-height: 0;
    opacity: 0;
    overflow: hidden;
  }
}
.md-button {
  min-width: 44px;
}
.composer-img-upload-progress {
  margin: auto !important;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
/* custom categories element styling */
.right-sn-categories {
  background-color: #6AA3C8;
  color: #fff;
  height: 40px;
  border-radius: 5px;
  margin: 5px;
  overflow: hidden;
  & .cat-dge {
    overflow: hidden;
    border-radius: 100px 0px 0px 100px;
  }
  & .cat-edge-text{
    color: white;
    font-weight: 900;
  }
  & .md-button {
    margin: 0;
    width: 100%;
  }
  & .cat-active i {
    display: inline-block !important;
    color: #fafafa;
    position: absolute;
    right: 10px;
    line-height: 30px;
  }
  & .cat-middle {
    background-color: rgba(255,255,255, 0.15);
    color: white;
    overflow: hidden;
    //border-radius: 0px 100px 100px 0px;
    & a {
      color: #fff;
      font-weight: bolder;
      //border-radius: 0px 100px 100px 0px;
    }
  }
}
/* flat buttons styling */
.btn-parent.flat {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  margin: 0 auto;
  height: 100%;
  width: 60px;
  & .md-button {
    line-height: 4;
    height: 100%;
    width: 100%;
    margin: 0;
  }
  &:hover {
    background: rgba(0, 0, 0, 0.1);
  }
}

.badge-notification{
  background: #F17467;
  position: absolute;
  right: 5px;
  top: 15px;
}

.md-sidenav-backdrop {
  -webkit-transition: all 1s !important;
  transition: all 1s !important;
    //opacity: 0 !important;
    //display: none !important;
}

md-sidenav.md-closed-add,
md-sidenav.md-closed-remove {
    -webkit-transition: all .5s ease-in-out !important;
    transition: all .5s ease-in-out !important;
}

.sidenav-items {
  padding: 0;
  & .mdi {
    margin: 10px;
    color: #5DADE0;
  }
  & .sidenav-menu-active {
    background: #eee;
  }
  & .sidenav-item .md-button {
    color: #777;
    font-weight: bolder;
    text-transform: none;
    text-align: left;
    width: 100%;
    display: block;
    padding-bottom: 5px;
    margin: 5px 0;
    & .mdi {
      color: #999;
    }
  }
}
.md-button.text-muted {
  color: #777 !important;
}
.md-button.btn-outline {
  border: solid 1px;
}
.md-button.no-text-transform {
  text-transform: none;
}
.bold-text{
  font-weight: 600;
}
.md-button.no-shadow {
  box-shadow: none !important;
}
.absolute-center-horizontal {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
}
